import React, { useCallback } from 'react';
import { Row, Col, Slider, InputNumber } from 'antd';

import { rowLayout } from '../config';
import { formatNumber, roundToStep } from '../utils';

const sanitize = (value, { precition, step }) => value !== undefined ?
  formatNumber(roundToStep(value, step), { position: -precition })
  : undefined;

export default ({
  min = 0,
  max = 1,
  value,
  onChange = () => { },
  step = 1,
  precition = -Math.floor(Math.log10(step)),
  unit,
}) => {
  const valueChange = useCallback(v => {
    onChange(sanitize(v, { precition, step }));
  }, [onChange, precition, step]);

  return (
    <Row {...rowLayout}>
      <Col xs={24} sm={16} md={16} lg={16} xl={16}>
        <Slider
          min={min}
          max={max}
          onChange={valueChange}
          value={typeof value === 'number' ? value : min}
          step={step}
          tipFormatter={v => `${v}${unit ? ` ${unit}` : ""}`}
          marks={{
            [min]: {
              style: {
                whiteSpace: "nowrap",
              },
              label: `${min}${unit ? ` ${unit}` : ""}`,
            },
            [max]: {
              style: {
                whiteSpace: "nowrap",
              },
              label: `${max}${unit ? ` ${unit}` : ""}`,
            },
          }}
        />
      </Col>
      <Col xs={24} sm={8} md={8} lg={8} xl={8}>
        <InputNumber
          min={min}
          max={max}
          style={{ width: "100%" }}
          step={step}
          value={value}
          onChange={valueChange}
        />
      </Col>
    </Row>
  );
}
