import { saveAs } from 'file-saver';
import { fileName } from "../config/index";
import { booleanMap, BOOLEAN_TYPES } from "../components/RadioButtons/YesNo";
import { calculationMap, CALCULATION_TYPES } from "../components/RadioButtons/AutoOrNot";
import { calculatePrevU, formatNumber } from "./index";
import { calculateRequiredWidth } from './calculations';
import {
  fieldNames,
  FIELDS,
  buildingAllowYears,
  partitionTypes,
  izolationTypes,
  izolationWidthConfig,
  requiredIzolation,
  materialCategoriesExamples,
} from "../config";

export const saveFile = (values) => {
  const contents = getFileContents(values);

  downloadFile(fileName, contents);
};

const getFileContents = (values) => {
  const {
    [FIELDS.BUILDING_YEAR]: buildingYear,
    [FIELDS.PREVIOUS_WARMED]: warmed,
    [FIELDS.SELF_BEFORE_CALC]: self,
    [FIELDS.PREV_IZOLATION_TYPE]: prevIzolation,
    [FIELDS.PREV_IZOLATION_WIDTH]: prevIzolationWidth,
    [FIELDS.MATERIAL_LAYERS]: materialLayers,
    [FIELDS.MODERNIZATION_ITEM]: modernizationItem,
    [FIELDS.PLANED_LAMBDA]: plannedLambda,
    [FIELDS.PLANED_MATERIAL]: plannedMaterial,
  } = values;

  const existingIzolation = warmed === BOOLEAN_TYPES.YES
    ? 
    `${fieldNames[FIELDS.PREV_IZOLATION_TYPE]}: ${izolationTypes[prevIzolation].description}
    ${fieldNames[FIELDS.PREV_IZOLATION_WIDTH]}: ${prevIzolationWidth} ${izolationWidthConfig.unit}
    `
    : '';

  const selfBeforeCalculations = self === CALCULATION_TYPES.BY_MYSELF
    ? 
    `${materialLayers.map((layer, index) => {
        return `
          Przegroda ${index + 1}.
          ${fieldNames[FIELDS.MATERIAL_CATEGORY]}: ${materialCategoriesExamples[layer.existingLayer].description}
          ${fieldNames[FIELDS.MATERIAL_CATEGORY_SPECIFIC]}: ${materialCategoriesExamples[layer.existingLayer].examples[layer.existingLayerSpecific].description}
          ${fieldNames[FIELDS.MATERIAL_CATEGORY_WIDTH]}: ${layer.existingLayerWidth} ${izolationWidthConfig.unit}
        `
    }).join('')}`
    : '';

  const autoBeforeCalculations = self === CALCULATION_TYPES.AUTOMATICALLY
    ?
    `
    Obliczenie współczynnika U przed ociepleniem dla przegrody:
    ${fieldNames[FIELDS.BUILDING_YEAR]}: ${buildingAllowYears[buildingYear].description}
    ${fieldNames[FIELDS.PREVIOUS_WARMED]} ${booleanMap[warmed]}
    ${existingIzolation}`
    : '';

  const result = (width => {
    return width > 0 
      ? `Minimalna wymagana grubość ocieplenia: ${Math.ceil(width)} ${izolationWidthConfig.unit}` 
      : "Przegroda nie wymaga docieplenia"
  })(calculateRequiredWidth(values));

  const plannedMaterialText =
    plannedMaterial === "otherMaterial"
      ? "Inny"
      : izolationTypes[plannedMaterial].description;

  return (
    `${fieldNames[FIELDS.MODERNIZATION_ITEM]}: ${partitionTypes[modernizationItem]}
    ${fieldNames[FIELDS.SELF_BEFORE_CALC]}: ${calculationMap[self]}
    ${autoBeforeCalculations}${selfBeforeCalculations}
    Wyliczone wartości współczynników U [W/m2K]:
    ${fieldNames[FIELDS.REQUIRED_IZOLATION]}: ${requiredIzolation[modernizationItem]}
    ${fieldNames[FIELDS.BEFORE_WARMING_U]}: ${formatNumber(calculatePrevU(values), { position: -3 })}
    
    ${fieldNames[FIELDS.PLANED_MATERIAL]}: ${plannedMaterialText}
    ${fieldNames[FIELDS.PLANED_LAMBDA]}: ${plannedLambda}

    ${fieldNames[FIELDS.RESULT]}
    ${result}
    `).replace(/  +/g, '') // replace removes indentation at the beginning of every row
      .replace(/\n/g, '\r\n'); 
};

const downloadFile = (fileName, text) => {
  var blob = new Blob([text], {type: "text/plain;charset=utf-8"});
  saveAs(blob, fileName);
};
