import React from 'react';
import {
  Button,
  Form,
  Typography,
  Row,
  Col,
} from 'antd';
import { DownloadOutlined } from '@ant-design/icons';

import {
  buildingAllowYears,
  columnsLayout,
  FIELDS,
  fieldNames,
  formLayout,
  lambdaSelectConfig,
  izolationTypes,
  izolationWidthConfig,
  partitionTypes,
  requiredIzolation,
  rowLayout,
  MAXIMUM_LAYERS,
} from './config';
import {
  BOOLEAN_TYPES,
  CALCULATION_TYPES,
  DecimalStep,
  DependentFields,
  Field,
  MaterialLayer,
  YesNo,
  AutoOrNot,
  Result,
  Select,
  Summary,
  RESULT_STATUSES,
  Advert,
} from './components';
import {
  canCalculatePrevU,
  calculatePrevU,
  formatNumber,
  saveFile,
} from './utils';
import { calculateRequiredWidth, canCalculateWidth } from './utils/calculations';

const OTHER_MATERIAL = "otherMaterial";

const { Option } = Select;
const { Text, Title } = Typography;

const initialFormValue = {
  [FIELDS.PREVIOUS_WARMED]: BOOLEAN_TYPES.NO,
  [FIELDS.SELF_BEFORE_CALC]: CALCULATION_TYPES.AUTOMATICALLY,
};

const preCalculateDependencies = [
  FIELDS.BUILDING_YEAR, FIELDS.PREVIOUS_WARMED, FIELDS.SELF_BEFORE_CALC, FIELDS.MODERNIZATION_ITEM,
  FIELDS.PREV_IZOLATION_TYPE, FIELDS.PREV_IZOLATION_WIDTH, FIELDS.MATERIAL_LAYERS];

const calculateDependencies = [
  ...preCalculateDependencies,
  FIELDS.PLANED_MATERIAL,
  FIELDS.PLANED_LAMBDA,
];

const Calculator = () => {
  const [form] = Form.useForm();

  // const onReset = () => {
  //   form.resetFields();
  // };

  const changePreviosWarmed = ({ target: { value } }) => {
    form.setFieldsValue({
      [FIELDS.SELF_BEFORE_CALC]: CALCULATION_TYPES.AUTOMATICALLY,
      [FIELDS.PREV_IZOLATION_TYPE]: undefined,
      [FIELDS.PREV_IZOLATION_WIDTH]: undefined,
      [FIELDS.MATERIAL_LAYERS]: undefined,
    });
  }

  const changeSelfCalc = ({ target: { value } }) => {
    form.setFieldsValue({
      [FIELDS.PREVIOUS_WARMED]: BOOLEAN_TYPES.NO,
      [FIELDS.PREV_IZOLATION_TYPE]: undefined,
      [FIELDS.PREV_IZOLATION_WIDTH]: undefined,
      [FIELDS.MATERIAL_LAYERS]: [{}],
      [FIELDS.BUILDING_YEAR]: undefined,
    });
  }

  const changePlanedMaterial = (value) => {
    form.setFieldsValue({
      [FIELDS.PLANED_LAMBDA]:
        value !== OTHER_MATERIAL
          ? izolationTypes[value].defaultValue / 1000
          : undefined,
    });
  };

  return (
    <Form {...formLayout} initialValues={initialFormValue} form={form} name="calculator">
      <Row {...rowLayout}>
        <Field name={FIELDS.MODERNIZATION_ITEM}>
          <Select>
            {Object.entries(partitionTypes).map(([key, description]) => (
              <Option value={key} key={key}>{description}</Option>
            ))}
          </Select>
        </Field>
      </Row>
      <Row {...rowLayout}>
          <Field name={FIELDS.SELF_BEFORE_CALC}>
            <AutoOrNot onChange={changeSelfCalc} />
          </Field>
      </Row>
      <DependentFields
        dependencies={[FIELDS.SELF_BEFORE_CALC]}
        requirement={(...args) => !selfCalcutation(...args)}
      >
        <Row {...rowLayout}>
          <Col {...columnsLayout.full}>
            <Title level={4}>
              Obliczenie współczynnika U przed ociepleniem dla przegrody
            </Title>
          </Col>
        </Row>
        <Row {...rowLayout}>
          <Field name={FIELDS.BUILDING_YEAR}>
            <Select>
              {Object.entries(buildingAllowYears).map(([key, { description }]) => (
                <Option value={key} key={key}>{description}</Option>
              ))}
            </Select>
          </Field>
        </Row>
        <Row {...rowLayout}>
          <Field name={FIELDS.PREVIOUS_WARMED}>
            <YesNo onChange={changePreviosWarmed} />
          </Field>
        </Row>
      </DependentFields>
      <DependentFields
        dependencies={[FIELDS.PREVIOUS_WARMED]}
        requirement={previoslyWarmed}
      >
        <Row {...rowLayout}>
          <Col {...columnsLayout.full}>
            <Title level={4}>
              Istniejąca izolacja
            </Title>
          </Col>
          <Field name={FIELDS.PREV_IZOLATION_TYPE}>
            <Select>
              {Object.entries(izolationTypes).map(([key, { description }]) => (
                <Option value={key} key={key}>{description}</Option>
              ))}
            </Select>
          </Field>
          <Field name={FIELDS.PREV_IZOLATION_WIDTH}>
            <DecimalStep
              {...izolationWidthConfig}
            />
          </Field>
        </Row>
      </DependentFields>
      <DependentFields
        dependencies={[FIELDS.SELF_BEFORE_CALC]}
        requirement={selfCalcutation}
      >
        <Row {...rowLayout}>
          <Col {...columnsLayout.full}>
            <Title level={4}>
              Samodzielne wyliczenie współczynnika U dla przegród przed ociepleniem
            </Title>
          </Col>
          <Form.List name={FIELDS.MATERIAL_LAYERS}>
            {(fields, { add, remove }) => (
              <>
                {fields.map((field, index) => (
                  <MaterialLayer.Wrapper {...columnsLayout.full} key={field.key}>
                    <Form.Item noStyle {...field}>
                      <MaterialLayer
                        counter={index}
                        first={index === 0}
                        last={index === MAXIMUM_LAYERS - 1}
                        remove={fields.length > 1 ? () => remove(field.name) : undefined}
                      />
                    </Form.Item>
                  </MaterialLayer.Wrapper>
                ))}
                {fields.length < MAXIMUM_LAYERS && (
                  <Field>
                    <MaterialLayer.Add add={add} />
                  </Field>
                )}
              </>
            )}
          </Form.List>
        </Row>
      </DependentFields>
      <Field.ValueProvider
        dependencies={preCalculateDependencies}
      >
        {(values) => (
          <Row {...rowLayout}>
            <Col {...columnsLayout.double}>
              <PreCalculations
                values={values}
              />
            </Col>
          </Row>
        )}
      </Field.ValueProvider>
      <Row {...rowLayout}>
        <Field name={FIELDS.PLANED_MATERIAL}>
          <Select onChange={changePlanedMaterial}>
            {Object.entries(izolationTypes).map(([key, { description }]) => (
              <Option value={key} key={key}>{description}</Option>
            ))}
            <Option value={OTHER_MATERIAL} key={OTHER_MATERIAL}>Inne</Option>
          </Select>
        </Field>
        <Field.ValueProvider
          dependencies={[FIELDS.PLANED_MATERIAL]}
        >
          {({ [FIELDS.PLANED_MATERIAL]: value }) =>
            (<Field name={FIELDS.PLANED_LAMBDA}>
              {(
                value === OTHER_MATERIAL ? (
                  <DecimalStep {...lambdaSelectConfig} />
                ) : (
                    <Select disabled={!value}>
                      {value && (
                        ({ from, to, defaultValue }) => [...Array(to - from + 1)].map((_, index) => (
                          <Option key={from + index} value={(from + index) / 1000}>
                            {`${(from + index) / 1000}${from + index === defaultValue ? " (domyślna)" : ""}`}
                          </Option>
                        ))
                      )(izolationTypes[value])}
                    </Select>
                  ))}
            </Field>
            )}
        </Field.ValueProvider>
      </Row>
      <Field.ValueProvider
        dependencies={calculateDependencies}
      >
        {(values) =>
          (canCalculate =>
            (<Row {...rowLayout}>
              <Col {...columnsLayout.default}>
                {canCalculate ? (
                  (width =>
                    (<Result
                      label={fieldNames[FIELDS.RESULT]}
                      status={RESULT_STATUSES.POSITIVE}
                      value={width > 0 ? `${Math.ceil(width)} cm` : undefined}
                    >
                      {width > 0 ? "Minimalna wymagana grubość ocieplenia: " : "Przegroda nie wymaga docieplenia"}
                    </Result>))(calculateRequiredWidth(values))
                ) : (
                    <Result
                      label={fieldNames[FIELDS.RESULT]}
                      status={RESULT_STATUSES.NEGATIVE}
                    >
                      Uzupełnij wszystkie pola przed obliczeniem
                    </Result>
                  )}
              </Col>
              <Col {...columnsLayout.default}>
                <Advert open={canCalculate}>
                  <div style={{paddingBottom: 16}}>Możesz zapisać wynik wykonanych obliczeń</div>
                  <Button 
                    icon={<DownloadOutlined />} 
                    onClick={() => saveFile(values)}
                  >
                    Zapisz
                  </Button>
                </Advert>
              </Col>
            </Row>)
          )(canCalculateWidth(values))}
      </Field.ValueProvider>
    </Form >
  );
};

const PreCalculations = ({
  values
}) => {
  const canCalculate = canCalculatePrevU(values);
  return (
    <Summary success={canCalculate}>
      <Row {...rowLayout}>
        <Col
          span={24}
          style={{ paddingBottom: 12 }}
        >
          <Text strong>Wyliczone wartości współczynników U [W/m2K]</Text>
        </Col>
        <Col {...columnsLayout.summaryColumn}>
          <Form.Item
            label={fieldNames[FIELDS.REQUIRED_IZOLATION]}
          >
            {values[FIELDS.MODERNIZATION_ITEM] ? (
              <Text strong>{requiredIzolation[values[FIELDS.MODERNIZATION_ITEM]]}</Text>
            ) : (
                <Text type="danger">Konieczny wybór rodzaju modernizowanej przegrody</Text>
              )}
          </Form.Item>
        </Col>
        <Col {...columnsLayout.summaryColumn}>
          <Form.Item
            label={fieldNames[FIELDS.BEFORE_WARMING_U]}
          >
            {canCalculate ? (
              <Text strong>{formatNumber(calculatePrevU(values), { position: -3 })}</Text>
            ) : (
                <Text type="danger">Konieczne uzupełnienie wszystkich pustych pól</Text>
              )}
          </Form.Item>
        </Col>
      </Row>
    </Summary>
  );
}

const previoslyWarmed = ({ getFieldValue }) => getFieldValue([FIELDS.PREVIOUS_WARMED]) === BOOLEAN_TYPES.YES;
const selfCalcutation = ({ getFieldValue }) => getFieldValue([FIELDS.SELF_BEFORE_CALC]) === CALCULATION_TYPES.BY_MYSELF;

export default Calculator;
