import React, { useCallback } from 'react';
import {
  Button,
  Col,
  Row,
  Tooltip,
  Typography,
  Grid,
} from 'antd';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';

import {
  FIELDS,
  fieldNames,
  izolationWidthConfig,
  materialCategoriesExamples,
  rowLayout,
  columnsLayout,
} from '../config';
import { cx } from '../utils';
import Select from './CustomSelect';
import DecimalStep from './DecimalStep';
import Field from './Field';
import styles from './MaterialLayer.module.scss';

const { Option } = Select;
const { useBreakpoint } = Grid;
const { Title } = Typography;

const sanitize = value => value || {};

const MaterialLayer = ({ counter, first, last, remove, value = {}, onChange = () => { } }) => {
  const valueChange = useCallback((v, key) => {
    onChange(sanitize({
      ...value,
      [key]: v,
    }));
  }, [value, onChange]);

  const categoryChange = useCallback(v => {
    onChange(sanitize({
      ...value,
      [FIELDS.MATERIAL_CATEGORY]: v,
      [FIELDS.MATERIAL_CATEGORY_SPECIFIC]: undefined,
    }));
  }, [value, onChange]);

  const breakpoints = useBreakpoint();
  const responsive = !breakpoints.lg;
  const fieldClassName = cx(!responsive && !last && styles.Row__New, responsive && styles.Row__Responsive);
  const showLabel = first || responsive;

  return (
    <Row {...rowLayout}>
      {!responsive && remove && (
        <Field
          label={first ? " " : undefined}
          layout={columnsLayout.minimal}
          className={cx(!last && styles.NewRow)}
        >
          <Tooltip
            title="Usuń warstwę"
            placement="topRight"
          >
            <Button
              type="link"
              icon={<MinusCircleOutlined />}
              onClick={() => remove()}
              danger
            />
          </Tooltip>
        </Field>
      )}
      {responsive && (
        <Col {...columnsLayout.full}>
          <Title className={styles.Title} level={4}>{`Warstwa nr ${counter + 1}`}</Title>
          {remove && (
            <Button
              type="link"
              icon={<MinusCircleOutlined />}
              onClick={() => remove()}
              danger
            > Usuń warstwę</Button>
          )}
        </Col>
      )}
      <Field
        layout={(!responsive && remove) ? columnsLayout.short : columnsLayout.default}
        className={fieldClassName}
        label={showLabel ? fieldNames[FIELDS.MATERIAL_CATEGORY] : undefined}
      >
        <Select
          value={value[FIELDS.MATERIAL_CATEGORY]}
          onChange={categoryChange}
        >
          {Object.entries(materialCategoriesExamples).map(([key, { description }]) => (
            <Option value={key} key={key}>{description}</Option>
          ))}
        </Select>
      </Field>
      <Field
        className={fieldClassName}
        label={showLabel ? fieldNames[FIELDS.MATERIAL_CATEGORY_SPECIFIC] : undefined}
      >
        {(category => category ? (
          <Select
            value={value[FIELDS.MATERIAL_CATEGORY_SPECIFIC]}
            onChange={v => valueChange(v, FIELDS.MATERIAL_CATEGORY_SPECIFIC)}
          >
            {materialCategoriesExamples[category].examples.map(({ description }, idx) => (
              <Option value={idx} key={`${idx}_${description}`}>{description}</Option>
            ))}
          </Select>
        ) : (
            <Select
              disabled
            />
          ))(value[FIELDS.MATERIAL_CATEGORY])}
      </Field>
      <Field
        className={fieldClassName}
        label={showLabel ? fieldNames[FIELDS.MATERIAL_CATEGORY_WIDTH] : undefined}
        layout={{...columnsLayout.default, sm: 24, md: 24}}
      >
        <DecimalStep
          value={value[FIELDS.MATERIAL_CATEGORY_WIDTH]}
          onChange={v => valueChange(v, FIELDS.MATERIAL_CATEGORY_WIDTH)}
          {...izolationWidthConfig}
        />
      </Field>
    </Row>
  );
};

const Add = ({ add, className, ...props }) => (
  <Button
    type="dashed"
    onClick={() => add()}
    className={cx(styles.Add, className)}
    {...props}
  >
    <PlusOutlined /> Dodaj warstwę
  </Button>);

const Wrapper = ({ children, ...props }) => (
  <Col {...props}>
    {children}
  </Col>
)

export default Object.assign(
  MaterialLayer,
  {
    Add,
    Wrapper,
  }
);
