import React from 'react';
import { Radio } from 'antd';
import { cx } from '../../utils';
import styles from './RadioButtons.module.scss';

export default ({className, values, valuesMap, ...props}) => (
  <Radio.Group buttonStyle="solid" className={cx(styles.RadioGroup, className)} {...props}>
    {Object.keys(values).map(value => (
      <Radio.Button className={styles.RadioItem} value={value} key={value}>{valuesMap[value]}</Radio.Button>
    ))}
  </Radio.Group>
);
