import React from 'react';
import { Col, Form } from 'antd';

import { columnsLayout, fieldNames } from '../config';

const Field = ({ layout = columnsLayout.default, name, children, ...props }) => (
  <Col {...layout}>
    <Form.Item
      {...(name ? {
        name,
        label: fieldNames[name],
        rules: [{
          required: true,
          message: 'Pole jest wymagane'
        }],
        validateTrigger: "onChange"
      } : {})}
      {...props}
    >
      {children}
    </Form.Item>
  </Col>);

const ValueProvider = ({ dependencies, children, ...props }) => (
  <Form.Item
    noStyle
    shouldUpdate={(prevValues, currentValues) => dependencies.some(x => prevValues[x] !== currentValues[x])}
    {...props}
  >
    {({ getFieldValue }) => children(dependencies.reduce(
      (params, key) => ({ ...params, [key]: getFieldValue([key]) }), {}
    ))}
  </Form.Item>);

export default Object.assign(
  Field,
  {
    ValueProvider,
  },
);
