import React from 'react';
import { Select } from 'antd';
import styles from './CustomSelect.module.scss';
import { cx } from '../utils';

const { Option } = Select;

const CustomSelect = ({ onClick, dropdownClassName, ...props }) => (<Select
  dropdownClassName={cx(styles.Select, dropdownClassName)}
  {...props}
/>);

export default Object.assign(CustomSelect, {
  Option,
});
