import React from 'react';

import { Form } from 'antd';

export default ({ dependencies, requirement = () => true, children, noMatch = null, ...props }) => (
  <Form.Item
    noStyle
    shouldUpdate={(prevValues, currentValues) => dependencies.some(x => prevValues[x] !== currentValues[x])}
    {...props}
  >
    {({ getFieldValue }) => requirement({ getFieldValue }) ? children : noMatch}
  </Form.Item>
);
