import { BOOLEAN_TYPES, CALCULATION_TYPES } from '../components';
import {
  FIELDS,
  buildingAllowYears,
  izolationTypes,
  Rsi,
  Rse,
  materialCategoriesExamples,
  requiredIzolation,
} from '../config';

export const canCalculatePrevU = (values) => {
  if (!values[FIELDS.MODERNIZATION_ITEM]) {
    return false;
  }
  if(values[FIELDS.SELF_BEFORE_CALC] === CALCULATION_TYPES.AUTOMATICALLY) {
    if(!values[FIELDS.BUILDING_YEAR]) {
      return false;
    }
    if (values[FIELDS.PREVIOUS_WARMED] === BOOLEAN_TYPES.YES) {
      return values[FIELDS.PREV_IZOLATION_TYPE]
        && values[FIELDS.PREV_IZOLATION_WIDTH];
    }
    return true;
  }
  else {
    return checkLayersData(values[FIELDS.MATERIAL_LAYERS]);
  }
};

const checkLayersData = (layers) => layers && layers.length > 0 && !layers.some(
  x => !(x && x[FIELDS.MATERIAL_CATEGORY] && x[FIELDS.MATERIAL_CATEGORY_SPECIFIC] !== undefined && x[FIELDS.MATERIAL_CATEGORY_WIDTH]));

export const calculatePrevU = (values) => {
  const {
    [FIELDS.BUILDING_YEAR]: year,
    [FIELDS.PREVIOUS_WARMED]: warmed,
    [FIELDS.SELF_BEFORE_CALC]: self,
    [FIELDS.MODERNIZATION_ITEM]: what,
    [FIELDS.PREV_IZOLATION_TYPE]: prevIzolation,
    [FIELDS.PREV_IZOLATION_WIDTH]: prevIzolationWidth,
    [FIELDS.MATERIAL_LAYERS]: materialLayers,
  } = values;
  if (self === CALCULATION_TYPES.BY_MYSELF) {
    return 1 / (layersResistance(materialLayers) + Rse + Rsi);
  }
  else {
    if (warmed === BOOLEAN_TYPES.YES) {
      return 1 / (layerResistance(prevIzolationWidth, izolationTypes[prevIzolation].defaultValue / 1000)
        + 1 / buildingAllowYears[year].avarageU[what])
    }
    else {
      return buildingAllowYears[year].avarageU[what];
    }
  }
}

export const canCalculateWidth = (values) => {
  const planedMaterial = values[FIELDS.PLANED_MATERIAL];
  const planedLambda = values[FIELDS.PLANED_LAMBDA];
  return canCalculatePrevU(values) && planedMaterial && planedLambda;
}

export const calculateRequiredWidth = (values) => {
  const planedLambda = values[FIELDS.PLANED_LAMBDA];
  const modernizedItem = values[FIELDS.MODERNIZATION_ITEM];

  const prevU = calculatePrevU(values);
  const requiredU = requiredIzolation[modernizedItem];

  return planedLambda * (1 / requiredU - 1 / prevU) * 100;
}

const layersResistance = (layers) => layers.reduce((res, {
  [FIELDS.MATERIAL_CATEGORY]: category,
  [FIELDS.MATERIAL_CATEGORY_SPECIFIC]: idx,
  [FIELDS.MATERIAL_CATEGORY_WIDTH]: width
}) =>
  res + layerResistance(width, materialCategoriesExamples[category].examples[idx].value), 0);

const layerResistance = (width, lambda) => width / 100 / lambda;
