import React from 'react';
import styles from './Header.module.scss';

import icon from '../assets/clearSkyLogo.png';

export default () => (
  <div className={styles.Header}>
    <div className={styles.Content}>
      <a className={styles.Logo} href="https://czystepowietrze.gov.pl/wez-dofinansowanie/" title="Czyste Powietrze">
        Program Czyste Powietrze
      </a>
      <h1 className={styles.Text}>Kalkulator grubości izolacji</h1>
      <img className={styles.Icon} src={icon} alt="Logo programu czyste powietrze" />
    </div>
  </div>
);
