import React from 'react';
import { Typography } from 'antd';
import { CSSTransition } from 'react-transition-group';
import { cx } from '../utils';
import { TIMEOUT } from './Advert';

import styles from './Result.module.scss';

const { Title, Text } = Typography;

export const RESULT_STATUSES = Object.freeze({
  NEUTRAL: 'neutral',
  POSITIVE: 'positive',
  NEGATIVE: 'negative',
});

export default ({
  className,
  value,
  label,
  children,
  status = RESULT_STATUSES.NEUTRAL,
  ...props
}) => (
  <div
    className={cx(
      styles.Result,
      status && {
        [RESULT_STATUSES.NEGATIVE]: styles.Result__Negative,
        [RESULT_STATUSES.NEUTRAL]: styles.Result__Neutral,
        [RESULT_STATUSES.POSITIVE]: styles.Result__Positive,
      }[status],
      className
    )}
    {...props}
  >
    {label && (<Title className={styles.Title} level={4}>{label}</Title>)}
    <Text className={styles.Content}>{children}</Text>
    {value && (<Value className={styles.Content}>{value}</Value>)}
  </div>
);

const Value = ({className, children, ...props}) => (
  <CSSTransition
    in
    appear
    timeout={TIMEOUT}
    classNames={{
      appear: styles.Appear,
      appearActive: styles.Appear__Active,
    }}
    {...props}
  >
    <Text className={cx(styles.Value, className)}>{children}</Text>
  </CSSTransition>
)
