import React from 'react';
import RadioButtons from './RadioButtons';

export const BOOLEAN_TYPES = Object.freeze({
  YES: "YES",
  NO: "NO",
});

export const booleanMap = {
  [BOOLEAN_TYPES.YES]: "TAK",
  [BOOLEAN_TYPES.NO]: "NIE",
};

export default ({className, ...props}) => (
  <RadioButtons className={className} values={BOOLEAN_TYPES} valuesMap={booleanMap} {...props} />
);
