import React from 'react';
import RadioButtons from './RadioButtons';

export const CALCULATION_TYPES = Object.freeze({
  BY_MYSELF: "BY_MYSELF",
  AUTOMATICALLY: "AUTOMATICALLY",
});

export const calculationMap = {
  [CALCULATION_TYPES.BY_MYSELF]: "Samodzielnie",
  [CALCULATION_TYPES.AUTOMATICALLY]: "Automatycznie",
};

export default ({className, ...props}) => (
  <RadioButtons className={className} values={CALCULATION_TYPES} valuesMap={calculationMap} {...props} />
);
