import React from 'react';
import { CSSTransition } from 'react-transition-group';
import { cx } from '../utils';

import styles from './Advert.module.scss';

export const TIMEOUT = 500;

const Advert = ({ open, className, children, ...props }) => (
  <CSSTransition
    in={Boolean(open)}
    unmountOnExit
    timeout={TIMEOUT}
    classNames={{
      enter: styles.Enter,
      enterActive: styles.Enter__Active,
      exit: styles.Exit,
      exitActive: styles.Exit__Active,
    }}
    {...props}
  >
    <div className={cx(styles.Advert, className)}>
      <div className={styles.Container}>
        <div className={styles.Arrow} />
        {children}
      </div>
    </div>
  </CSSTransition>
);

export default Advert;
